.section,
.footer {
  opacity: 0;
  transform: translateY(0);
  transition:
    opacity 400ms ease,
    transform 400ms ease;
}

.hero {

  &__content {
    opacity: 0;
    transform: translateX(0);
    transition:
      opacity 400ms ease,
      transform 400ms ease;
  }

  &__image {
    opacity: 0;
    transform: translateX(0);
    transition:
      opacity 400ms ease,
      transform 400ms ease;
  }
}

.transition-out {

  .section,
  .footer {
    opacity: 0;
    transform: translateY(10%);
  }

  .hero {


    &__content {
      opacity: 0;
      transform: translateX(-10%);
    }

    &__image {
      opacity: 0;
      transform: translateX(10%);
    }
  }
}

.transition-in {

  .hero {

    &__content {
      opacity: 1;
      transform: translateX(0);
    }

    &__image {
      opacity: 1;
      transform: translateX(0);
    }
  }

  .section,
  .footer {
    opacity: 1;
    transform: translateY(0);
  }
}
